import Vue from 'vue'
import VueRouter from 'vue-router'

const str = /(phone|pod|iPhone|iPod|Android|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'HomeView',
    meta: {
      title: 'LockStore - 不一样的态度'
    },
    component: () => import('@/views/HomeView.vue'),
    beforeEnter (to, from, next) {
      const viewer = navigator.userAgent.match(str)
      if(!viewer){
        return next('/windows')
      }else next()
    }
  },
  {
    path: '/resources',
    name: 'ResourcesView',
    meta: {
      title: 'LockStore - 资源'
    },
    component: () => import('@/views/ResourcesView.vue'),
    beforeEnter (to, from, next) {
      const viewer = navigator.userAgent.match(str)
      if(!viewer){
        return next('/windows')
      }else next()
    }
  },
  {
    path: '/windows',
    name: 'WindowsView',
    meta: {
      title: 'LockStore - 不一样的态度'
    },
    component: () => import('@/views/WindowsView.vue'),
    beforeEnter (to, from, next) {
      const viewer = navigator.userAgent.match(str)
      if(viewer){
        return next('/')
      }else next()
    }
  },
  {
    path: '/windowsResources',
    name: 'WindowsResourcesView',
    meta: {
      title: 'LockStore - 资源'
    },
    component: () => import('@/views/WindowsResourcesView.vue'),
    beforeEnter (to, from, next) {
      const viewer = navigator.userAgent.match(str)
      if(viewer){
        return next('/')
      }else next()
    }
  },
  {
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  window.document.title = to.meta.title
  next()
})

export default router
