<template>
  <div id="app" :style="appWidth">
    <router-view/>
    <!-- 底部菜单栏 -->
    <van-tabbar route v-model="active" v-if="tabbarSta">
      <van-tabbar-item replace to="/">
        <template #icon="props">
          <img
            :src="props.active ? home_icon.active : home_icon.inactive"
            class="tabbar-icon"
          />
        </template>
      </van-tabbar-item>
      <van-tabbar-item replace to="/resources">
        <template #icon="props">
          <img
            :src="props.active ? resources_icon.active : resources_icon.inactive"
            class="tabbar-icon"
          />
        </template>
      </van-tabbar-item>
    </van-tabbar>
    <!-- 下载框 -->
    <van-action-sheet class="action-sheet" v-model="$store.state.actionSheetSta" :title="downLoadTitle">
      <p class="action-sheet-content" v-if="$store.state.downLoadContent">{{ $store.state.downLoadContent }}</p>
      <div class="action-sheet-icon">
        <span v-for="(item, index) in downLoadData" :key="index" :style="item.lock_type | lockTypeBackgroundColor" @click="downLoad(item.dowmload_src)">
          <p>{{ item.lock_type | lockTypeText }}</p>
        </span>
      </div>
      <div class="action-sheet-tips">注：使用白色锁头不要白色类锁屏壁纸，黑色类同。</div>
    </van-action-sheet>
    <!-- 大屏端下载框 -->
    <van-dialog v-model="$store.state.downLoadCardSta" :title="downLoadTitle" confirmButtonText="取消" confirmButtonColor="#6CC8AE">
      <div class="downLoad">
        <p class="downLoad-content" v-if="$store.state.downLoadContent">{{ $store.state.downLoadContent }}</p>
        <span v-for="(item, index) in downLoadData" :key="index" :style="item.lock_type | lockTypeBackgroundColor" @click="downLoadCardDown(item.dowmload_src)">
          <p>{{ item.lock_type | lockTypeText }}</p>
        </span>
      </div>
    </van-dialog>
    <!-- 加载框 -->
    <van-overlay :show="$store.state.loadingSta" class-name="overlay">
      <div class="overlay-loading">
        <van-loading vertical color="#ffffff">请稍等</van-loading>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { ImagePreview } from 'vant'
export default {
  name: 'app',
  comments: {
    [ImagePreview.Component.name]: ImagePreview.Component
  },
  data () {
    return {
      active: 0,
      home_icon: {
        active: require('@/assets/home-active.png'),
        inactive: require('@/assets/home.png')
      },
      resources_icon: {
        active: require('@/assets/resources-active.png'),
        inactive: require('@/assets/resources.png')
      },
      tabbarSta: false,
      appWidth: null,
    }
  },
  beforeUpdate() {
    if(this.$route.path == '/windows' || this.$route.path == '/windowsResources') return this.tabbarSta = false
    return this.tabbarSta = true
  },
  methods: {
    downLoad (src) {
      window.location.href = src
      this.$store.commit('changeActionSheetSta')
    },
    downLoadCardDown (src) {
      window.location.href = src
      this.$store.commit('changeDownLoadCardSta')
    }
  },
  computed: {
    ...mapState(['tipsText', 'downLoadData', 'loading', 'imgUrl', 'downLoadFileName']),
    downLoadTitle () {
      return `下载 - ${this.downLoadFileName}`
    }
  },
  filters: {
    lockTypeBackgroundColor (type) {
      const black = 'background:#000000;color:#ffffff;'
      const white = 'border: 2px solid #000000;box-sizing: border-box;color:#000000;'
      const color = 'background:linear-gradient(45deg, #64bfe9, #74d178);color:#ffffff;'
      return type === 0 ? black : type === 1 ? white : type === 2 ? color : null
    },
    lockTypeText (type) {
      return type === 0 ? '黑色' : type === 1 ? '白色' : type === 2 ? '彩色' : null
    }
  },
  watch: {
    tipsText (text) { // 监视提示文本，内容变更显示提示框
      if (text === null) return
      this.$dialog.alert({
        message: text,
        confirmButtonColor: '#6CC8AE'
      }).then(() => {
        this.$store.commit('tips', null)
      })
    },
    loading (status) {
      if (!status) {
        document.body.style.overflow = ''
        document.removeEventListener('touchmove', function (e) { e.preventDefault() }, false)
      } else {
        document.body.style.overflow = 'hidden'
        document.addEventListener('touchmove', function (e) { e.preventDefault() }, false)
      }
    },
    imgUrl (url) { // 监视图片预览框的图片地址，内容改变则显示图片预览框
      if (url === null) return
      const that = this
      ImagePreview({
        images: [
          url
        ],
        showIndex: false,
        closeable: true,
        onClose () {
          that.$store.commit('showImg', null)
        }
      })
    }
  }
}
</script>

<style lang="scss">
