  padding: 0;
  margin: 0;
}
@font-face {
  font-family: hnbbh;
  src: url('http://150.158.149.195/files/hnbbh.otf');
}
#app {
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #000000;
  h1{
    font-size: 25px;
  }
  .tabbar-icon {
    position: relative;
    top: 2px;
    transform: scale(1.35);
  }
  .action-sheet{
    display: flex;
    .action-sheet-content{
      margin-bottom: 10px;
      color: #6CC8AE;
    }
    .action-sheet-icon{
      width: 90%;
      position: relative;
      left: 50%;
      transform: translate(-50%, 0) !important;
      display: flex;
      align-items: center;
      justify-content: space-between;
      span{
        width: 100%;
        height: 45px;
        border-radius: 20px;
        margin: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bolder;
        transition: all .3s;
      }
    }
    .action-sheet-tips{
      padding: 5px 16px;
      font-size: 14px;
      color: #979797;
      margin: 30px 0px;
      margin-top: 15px;
    }
  }
  .overlay{
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    .overlay-loading{
      width: 100px;
      height: 100px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #000000b3;
      filter: drop-shadow(0px 10px 25px #d8d8d8);
      border-radius: 20px;
    }
  }
  .downLoad{
    width: 80%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    .downLoad-content{
      margin-top: 15px;
      color: #787878;
    }
    span{
      width: 100%;
      height: 45px;
      border-radius: 20px;
      margin: 6px 0px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bolder;
      transition: all .3s;
    }
    span:first-of-type{
      margin-top: 20px;
    }
    span:last-of-type{
      margin-bottom: 20px;
    }
  }
}
</style>
