import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import {
  Button,
  Tabbar,
  TabbarItem,
  Image as VanImage,
  Lazyload,
  Overlay,
  Loading,
  ActionSheet,
  Dialog

} from 'vant'

Vue.use(Button)
Vue.use(Tabbar)
Vue.use(TabbarItem)
Vue.use(VanImage)
Vue.use(Lazyload)
Vue.use(Overlay)
Vue.use(Loading)
Vue.use(ActionSheet)
Vue.use(Dialog)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
