import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    actionSheetSta: false, // 下载框显示状态
    loadingSta: false, // 加载框显示状态
    downLoadSta: false, // 下载操作状态
    downLoadContent: null, // 下载描述
    downLoadData: null, // 下载数据
    tipsText: null, // 提示框提示文本
    imgUrl: null, // 图片预览框的图片地址
    downLoadCardSta: false, // 大屏端下载框状态
    downLoadFileName: null // 下载的文件名
  },
  getters: {

  },
  mutations: {
    changeActionSheetSta (state, data) {
      this.state.downLoadContent = data.content == 'null' || data.content == '' ? '' : `" ${data.content} "`
      this.state.downLoadFileName = data.name
      delete (data.content)
      delete (data.name)
      this.state.downLoadData = data
      this.state.actionSheetSta = !this.state.actionSheetSta
    },
    changeDownLoadCardSta (state, data) {
      this.state.downLoadContent = data.content == 'null' || data.content == '' ? '' : `" ${data.content} "`
      this.state.downLoadFileName = data.name
      delete (data.content)
      delete (data.name)
      this.state.downLoadData = data
      this.state.downLoadCardSta = !this.state.downLoadCardSta
    },
    changeDownLoadSta (state, status = true) {
      this.state.downLoadSta = status
    },
    loading (state, status = true) {
      this.state.loadingSta = status
    },
    tips (state, text = '提示框') {
      this.state.tipsText = text
    },
    showImg (state, url) {
      this.state.imgUrl = url
    }
  },
  actions: {

  },
  modules: {

  }
})
